/* Schedule.css */
.schedule-page {
  animation: fadeIn 0.5s ease;
}

.schedule-page h1 {
  text-align: center;
  color: var(--asu-maroon);
  margin-bottom: 40px;
  font-size: 2.25rem;
  font-weight: 800;
  position: relative;
  display: inline-block;
  left: 50%;
  transform: translateX(-50%);
}

.schedule-page h1::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background-color: var(--asu-gold);
  margin: 10px auto 0;
  border-radius: 2px;
}

.schedule-list {
  list-style-type: none;
  padding: 0;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.schedule-item {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  padding: 20px 24px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  transition: all 0.2s ease;
  display: grid;
  grid-template-columns: 100px 1fr auto;
  align-items: center;
  gap: 24px;
  position: relative;
  overflow: hidden;
}

.schedule-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
  border-color: rgba(140, 29, 64, 0.2);
}

.schedule-item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 6px;
  background-color: var(--text-light);
  /* Default neutral */
}

.schedule-item.home::before {
  background-color: var(--asu-maroon);
}

.schedule-item.away::before {
  background-color: var(--asu-gold);
}

/* Date & Time Column */
.game-date-time {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-right: 20px;
  border-right: 1px solid var(--border-color);
}

.game-date {
  font-weight: 700;
  font-size: 1.1rem;
  color: var(--text-primary);
  text-transform: uppercase;
  margin-bottom: 4px;
  line-height: 1.2;
}

.game-time {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-weight: 500;
}

/* Game Info Column */
.game-details {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.game-opponent {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 8px;
}

.status-indicator {
  font-size: 0.8rem;
  font-weight: 600;
  padding: 2px 6px;
  border-radius: 4px;
  background-color: var(--light-bg);
  color: var(--text-secondary);
  text-transform: uppercase;
}

.schedule-item.home .status-indicator {
  background-color: rgba(140, 29, 64, 0.1);
  color: var(--asu-maroon);
}

.schedule-item.away .status-indicator {
  background-color: rgba(255, 198, 39, 0.2);
  color: #7A5C00;
  /* Darker gold for text */
}

.game-notes {
  font-size: 0.85rem;
  font-style: italic;
  color: var(--text-light);
  margin-left: 8px;
}

.game-location {
  font-size: 0.9rem;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  gap: 6px;
}

/* Result / Actions Column */
.game-result {
  font-weight: 700;
  font-size: 1.25rem;
  padding: 8px 16px;
  border-radius: var(--border-radius-sm);
  background-color: var(--light-bg);
  color: var(--text-primary);
  white-space: nowrap;
}

/* Responsive */
@media (max-width: 768px) {
  .schedule-item {
    grid-template-columns: 1fr;
    gap: 12px;
    text-align: center;
    padding: 20px;
  }

  .game-date-time {
    flex-direction: row;
    justify-content: center;
    gap: 10px;
    border-right: none;
    border-bottom: 1px solid var(--border-color);
    padding-right: 0;
    padding-bottom: 12px;
    width: 100%;
  }

  .game-details {
    align-items: center;
  }

  .game-opponent {
    justify-content: center;
    flex-wrap: wrap;
  }

  .game-result {
    width: 100%;
    margin-top: 8px;
  }

  .schedule-item::before {
    width: 100%;
    height: 4px;
    top: 0;
    bottom: auto;
    left: 0;
    right: 0;
  }
}