/* Stats.css - Stat Lab Theme */
.stats-page {
  animation: fadeIn 0.5s ease;
  padding-bottom: 60px;
}

.stat-lab-header {
  text-align: center;
  margin-bottom: 50px;
}

.stat-lab-header h1 {
  font-size: 3rem;
  font-weight: 800;
  color: var(--asu-maroon);
  margin-bottom: 10px;
  letter-spacing: -0.05em;
  text-transform: uppercase;
}

.stat-lab-header .subtitle {
  font-size: 1.2rem;
  color: var(--text-secondary);
  font-weight: 500;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

/* --- Leaders Section (KPI Cards) --- */
.leaders-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin-bottom: 60px;
}

.leader-card {
  background: white;
  border-radius: var(--border-radius-lg);
  padding: 24px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-color);
  position: relative;
  overflow: hidden;
}

.leader-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(to right, var(--asu-maroon), var(--asu-gold));
}

.leader-card h3 {
  color: var(--text-secondary);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-top: 0;
  margin-bottom: 20px;
  font-weight: 700;
}

.leader-item {
  display: flex;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid var(--light-bg);
}

.leader-item:last-child {
  border-bottom: none;
}

.leader-item .rank {
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--asu-gold);
  width: 40px;
  text-align: center;
  line-height: 1;
}

.leader-item .player-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0 15px;
}

.player-name {
  font-weight: 700;
  color: var(--text-primary);
  font-size: 1.1rem;
}

.player-team {
  font-size: 0.75rem;
  color: var(--text-light);
  text-transform: uppercase;
}

.metric-value {
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--asu-maroon);
}

/* --- Data Table Controls --- */
.stats-control-panel {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 30px;
}

.control-btn {
  background: white;
  border: 1px solid var(--border-color);
  padding: 10px 30px;
  border-radius: 30px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  color: var(--text-secondary);
}

.control-btn:hover {
  border-color: var(--asu-maroon);
  color: var(--asu-maroon);
}

.control-btn.active {
  background: var(--asu-maroon);
  color: white;
  border-color: var(--asu-maroon);
  box-shadow: var(--shadow-sm);
}

/* --- Modern Table --- */
.stat-lab-table-container {
  overflow-x: auto;
  background: white;
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
}

.stat-lab-table {
  width: 100%;
  border-collapse: collapse;
  white-space: nowrap;
}

.stat-lab-table th {
  background: var(--light-bg);
  color: var(--text-secondary);
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.8rem;
  letter-spacing: 0.05em;
  padding: 16px 20px;
  text-align: left;
  position: sticky;
  top: 0;
}

.stat-lab-table td {
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color);
  color: var(--text-primary);
  font-variant-numeric: tabular-nums;
}

.stat-lab-table tr:last-child td {
  border-bottom: none;
}

.stat-lab-table tr:hover td {
  background-color: var(--light-bg);
}

/* Scrollbar Style */
.custom-scrollbar::-webkit-scrollbar {
  height: 8px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #ddd;
  border-radius: 4px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #ccc;
}

@media (max-width: 768px) {
  .stat-lab-header h1 {
    font-size: 2rem;
  }

  .leaders-section {
    grid-template-columns: 1fr;
  }
}