/* Home.css */
.home-page {
  animation: fadeIn 0.5s ease;
}

/* Hero Section */
.hero {
  background: linear-gradient(135deg, var(--asu-maroon) 0%, var(--asu-maroon-dark) 100%);
  color: var(--white);
  padding: 80px 24px;
  text-align: center;
  margin-bottom: 40px;
  position: relative;
  overflow: hidden;
}

.hero::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('https://thesundevils.com/images/2023/8/16/Mullett_Arena_Exterior_Web.jpg?width=1920&quality=80&format=jpg') no-repeat center center/cover;
  opacity: 0.15;
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  margin: 0 auto;
}

.hero h1 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 16px;
  letter-spacing: -0.02em;
}

.hero p {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin: 0 auto;
}

/* Sections */
.home-page>section,
.two-column>section {
  margin-bottom: 60px;
}

/* Section Header */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  border-bottom: 2px solid var(--border-color);
  padding-bottom: 12px;
}

.section-header h2,
.home-page h2 {
  font-size: 1.75rem;
  color: var(--asu-maroon);
  margin: 0;
  font-weight: 700;
}

/* View More Links */
.view-more {
  text-align: center;
  margin-top: 24px;
}

.view-more a {
  display: inline-block;
  padding: 10px 24px;
  background-color: var(--white);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.2s;
  box-shadow: var(--shadow-sm);
}

.view-more a:hover {
  background-color: var(--asu-gold);
  border-color: var(--asu-gold);
  color: var(--asu-maroon);
  transform: translateY(-2px);
}

/* Two Column Layout */
.two-column {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

/* Stats Cards */
.spotlight-content {
  display: grid;
  gap: 20px;
}

.stat-card {
  background: var(--white);
  padding: 24px;
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-sm);
  text-align: center;
  border: 1px solid var(--border-color);
  transition: transform 0.2s;
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
  border-color: var(--asu-gold);
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 800;
  color: var(--asu-maroon);
  line-height: 1;
  margin-bottom: 8px;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 600;
}

.hometown-map {
  background: var(--white);
  padding: 24px;
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
}

.hometown-map h3 {
  margin-top: 0;
  color: var(--text-primary);
}

.hometown-map img {
  width: 100%;
  height: auto;
  border-radius: 4px;
}

/* Responsive */
@media (max-width: 768px) {
  .hero {
    padding: 60px 20px;
  }

  .hero h1 {
    font-size: 1.8rem;
  }

  .two-column {
    grid-template-columns: 1fr;
    padding: 0 20px;
  }

  .spotlight-content {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

/* --- Game Day Mode --- */
.game-day-ticker {
  background: var(--asu-gold);
  color: var(--asu-maroon);
  font-weight: 800;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-size: 0.9rem;
  overflow: hidden;
  white-space: nowrap;
}

.ticker-content {
  display: inline-block;
  animation: scrollTicker 20s linear infinite;
  padding-left: 100%;
}

@keyframes scrollTicker {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-100%);
  }
}

.hero.hero-game-day {
  background: linear-gradient(135deg, #000 0%, var(--asu-maroon-dark) 80%);
  border-bottom: 4px solid var(--asu-gold);
  padding: 60px 24px;
}

.game-day-badge {
  background: var(--asu-gold);
  color: var(--asu-maroon);
  padding: 6px 16px;
  font-weight: 800;
  font-size: 0.9rem;
  border-radius: 4px;
  margin-bottom: 20px;
  display: inline-block;
}

.game-day-hero-content h1 {
  font-size: 3.5rem;
  margin-top: 10px;
}

.game-day-hero-content .vs {
  font-family: serif;
  font-style: italic;
  color: var(--asu-gold);
  font-size: 2rem;
  vertical-align: middle;
  margin: 0 10px;
}

.game-info-hero {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin: 30px 0;
  background: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: var(--border-radius-md);
  display: inline-flex;
}

.info-block {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.info-block .label {
  font-size: 0.8rem;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 5px;
}

.info-block .value {
  font-size: 1.4rem;
  font-weight: 700;
  color: white;
}

.hero-actions {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.btn-primary,
.btn-secondary {
  display: inline-block;
  padding: 12px 30px;
  border-radius: 30px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s;
}

.btn-primary {
  background: var(--asu-gold);
  color: var(--asu-maroon);
  border: 2px solid var(--asu-gold);
}

.btn-secondary {
  background: transparent;
  color: white;
  border: 2px solid white;
}

.btn-primary:hover,
.btn-secondary:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.btn-secondary:hover {
  background: white;
  color: var(--asu-maroon);
}

@media (max-width: 768px) {
  .game-day-hero-content h1 {
    font-size: 2rem;
  }

  .game-info-hero {
    flex-direction: column;
    gap: 15px;
    width: 100%;
  }

  .hero-actions {
    flex-direction: column;
  }
}