/* Roster.css */
.roster-page {
  animation: fadeIn 0.5s ease;
  max-width: 1200px;
  margin: 0 auto;
  padding: 24px;
}

.roster-page h1 {
  text-align: center;
  margin-bottom: 40px;
  color: var(--asu-maroon);
  font-size: 2.25rem;
  font-weight: 800;
  position: relative;
}

.roster-page h1::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background-color: var(--asu-gold);
  margin: 10px auto 0;
  border-radius: 2px;
}

/* Table Container (Card style) */
.roster-table-container {
  background: var(--white);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  overflow: hidden;
  /* For improved border radius clipping */
  margin-bottom: 40px;
}

/* Table Styles */
.roster-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.95rem;
}

.roster-table thead th {
  background-color: var(--asu-maroon);
  color: var(--white);
  padding: 16px 20px;
  text-align: left;
  font-weight: 600;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-bottom: 3px solid var(--asu-gold);
  white-space: nowrap;
}

.roster-table tbody tr {
  border-bottom: 1px solid var(--border-color);
  transition: background-color 0.1s;
}

.roster-table tbody tr:last-child {
  border-bottom: none;
}

.roster-table tbody tr:hover {
  background-color: rgba(255, 198, 39, 0.05);
  /* Very light gold */
}

.roster-table td {
  padding: 14px 20px;
  color: var(--text-primary);
  vertical-align: middle;
}

/* Specific Columns Styling */
.roster-table td:first-child {
  /* Number */
  font-family: 'Inter', monospace;
  font-weight: 700;
  color: var(--asu-maroon);
}

.roster-table td:nth-child(2) a {
  /* Name */
  color: var(--text-primary);
  font-weight: 700;
  text-decoration: none;
  font-size: 1rem;
}

.roster-table td:nth-child(2) a:hover {
  color: var(--asu-maroon);
  text-decoration: underline;
}

.flag-icon {
  width: 20px;
  height: auto;
  border-radius: 2px;
  margin-right: 8px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  vertical-align: middle;
  position: relative;
  top: -1px;
}

/* Loading/Error */
.loading-message,
.error-message {
  text-align: center;
  padding: 60px 20px;
  color: var(--text-secondary);
}

/* Responsive Table */
@media (max-width: 900px) {
  .roster-table-container {
    overflow-x: auto;
    border-radius: var(--border-radius-md);
  }

  .roster-table thead th {
    font-size: 0.8rem;
    padding: 12px 16px;
  }

  .roster-table td {
    padding: 12px 16px;
    font-size: 0.9rem;
    white-space: nowrap;
  }
}